import ShuShiCarbGameManager from "../../../ShuShiCarb.GameManager";
import Global from "../../../ShuShiCarb.Global";
import ShuShiCarbShopView from "./ShuShiCarb.ShopView";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ShuShiCarbItemTimeHappy extends cc.Component {

    @property(cc.Label)
    lbPrice: cc.Label = null;
    @property(cc.Node)
    nBtnBuy: cc.Node = null;
    @property(cc.Node)
    nStateBuy: cc.Node = null;
    @property(cc.Node)
    nCheckmask: cc.Node = null;

    @property(cc.Label)
    lbLeverSpeedOld: cc.Label = null;
    @property(cc.Label)
    lbLeverSpeedNew: cc.Label = null;

    index = 0;
    isCheck = false;
    _data = null
    isClick = false;

    onLoad () {
        this.index = JSON.parse(cc.sys.localStorage.getItem("itemIndexTimeHappy")) || 0;
        this.loadPurchaseState();
        this.checkClick();
        this.updatePrice(this.index);
    }


    loadPurchaseState() {
        const purchaseData = JSON.parse(cc.sys.localStorage.getItem("itemTimeHappy")) || [];
        for (let i = 0; i < Global.dataBagMoney.length; i++) {
            if (purchaseData[i]) {
                Global.dataBagMoney[i].isBuy = purchaseData[i].isBuy;
                Global.dataBagMoney[i].gold = purchaseData[i].gold;
                this.nStateBuy.children[i].active = purchaseData[i].isBuy;
            }
        }
    }

    savePurchaseState() {
        const purchaseData = Global.dataTimeHappy.map(item => ({
            isBuy: item.isBuy,
            gold: item.time
        }));
        cc.sys.localStorage.setItem('itemTimeHappy', JSON.stringify(purchaseData));
        cc.sys.localStorage.setItem('activeIndexTimeHappy', JSON.stringify(Global.activeIndexTimeHappy));
        cc.sys.localStorage.setItem('itemIndexTimeHappy', this.index.toString());
        cc.sys.localStorage.setItem('timeHappy', JSON.stringify(Global.timeHappy));
    }
    checkClick() {
        if(this.index < Global.dataHook.length && Global.totalGold >= Global.dataHook[this.index].price) { 
            this.nCheckmask.active = false;
            this.isCheck = true; 
         }
        else {
            this.nCheckmask.active = true;
            this.isCheck = false;
            this.nBtnBuy.getComponent(cc.Toggle).interactable = false;
        }
        console.log(this.index);
    }

    onClickBuy() {
        if(this.isCheck && this.index < Global.dataTimeHappy.length) {
            Global.totalGold -= Global.dataTimeHappy[this.index].price;
            Global.timeHappy += Global.dataTimeHappy[this.index].time;
            Global.dataTimeHappy[this.index].isBuy = true;
            this.nStateBuy.children[this.index].active = Global.dataTimeHappy[this.index].isBuy;
            Global.activeIndexTimeHappy = this.index;
            
            this.index++;
            this.checkClick();
            this.savePurchaseState();
            ShuShiCarbGameManager.instance.updateTotalGold();
            ShuShiCarbShopView.instace.updateGold();
            this.updatePrice(this.index);
        }else {
            this.nCheckmask.active = true;
            this.nBtnBuy.getComponent(cc.Toggle).interactable = false;
        }
    }

    updatePrice(index) {
        if(index < Global.dataTimeHappy.length) {
            this.lbPrice.string = Global.dataTimeHappy[index].price + ' ';
            this.lbLeverSpeedNew.string = '+' + Global.dataTimeHappy[index].time + '$';
        }else {
            this.lbPrice.string = "Max";
            this.lbLeverSpeedNew.string = "Max";
        }
    }
    // LIFE-CYCLE CALLBACKS:

    
    start () {

    }

    // update (dt) {}
}
